const exampleInitialState = {
  lastUpdate: 0,
  light: false,
  count: 0,
  GOLD_LIST:[]
}

export const actionTypes = {
  TICK: 'TICK',
  INCREMENT: 'INCREMENT',
  DECREMENT: 'DECREMENT',
  RESET: 'RESET'
}

// REDUCERS
export const reducer = (state = exampleInitialState, action) => {
  switch (action.type) {
    case actionTypes.TICK:
      return Object.assign({}, state, {
        lastUpdate: action.ts,
        light: !!action.light
      })
    case actionTypes.INCREMENT:
      return Object.assign({}, state, {
        count: state.count + 1
      })
    case actionTypes.DECREMENT:
      return Object.assign({}, state, {
        count: state.count - 1
      })
    case actionTypes.RESET:
      return Object.assign({}, state, {
        count: exampleInitialState.count
      })
    case GET_LIST:
      console.log('change-------------111')
      console.log(action.list)
      console.log('change-------------222')
      return Object.assign({}, state, {
        GOLD_LIST:[{
          id: '5e0ed750e51d4541216290b2',
          title: '一念之差，错失良机！浅谈基础类型之Symbol，给你的笔试多加一分~',
          url: 'https://juejin.im/post/5e0ea4c0f265da5d3a58ee66',
          originalUrl: 'https://juejin.im/post/5e0ea4c0f265da5d3a58ee66',
          date: { __type: 'Date', iso: '2020-01-03T05:55:29.158Z' },
          viewCount: 644,
          collectionCount: 4,
          user: {
            username: '.Ping',
            avatar: 'https://user-gold-cdn.xitu.io/2019/12/10/16eeeacbcb840dfa?w=300&h=300&f=png&s=61315',
            url: 'https://juejin.im/user/5d534b93518825378d5d6513'
          }
        }
      ]
      })
    default:
       return state
  }
}

// ACTIONS
export const serverRenderClock = (isServer) => dispatch => {
  return dispatch({ type: actionTypes.TICK, light: !isServer, ts: Date.now() })
}

export const startClock = dispatch => {
  return setInterval(() => {
    // Dispatch `TICK` every 1 second
    dispatch({ type: actionTypes.TICK, light: true, ts: Date.now() })
  }, 1000)
}

export const incrementCount = () => dispatch => {
  return dispatch({ type: actionTypes.INCREMENT })
}

export const decrementCount = () => dispatch => {
  return dispatch({ type: actionTypes.DECREMENT })
}

export const resetCount = () => dispatch => {
  return dispatch({ type: actionTypes.RESET })
}

export function initializeStore (initialState = exampleInitialState) {
  return createStore(reducer, initialState, composeWithDevTools(applyMiddleware(thunkMiddleware )))
}